.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: white;
  padding: 40px;
  border-radius: 10px;
  width: 800px;
  max-width: 90%;
  position: relative;
  display: flex;
  flex-direction: column;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.modal-content h3 {
  text-align: center;
}

.close-button {
  position: absolute;
  top: 20px;
  right: 20px;
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
}

.modal-header {
  display: flex;
  align-items: flex-start;
  margin-bottom: 20px;
  flex: 1;
}

.lab-info {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-right: 20px;
  flex: 1;
}

.lab-image {
  width: 100px;
  height: 100px;
  background-color: #ddd;
  border-radius: 50%;
  margin-bottom: 10px;
}

.lab-contact {
  text-align: center;
}

.lab-name {
  font-size: 20px;
  margin: 0;
  margin-bottom: 10px;
  text-decoration: underline;
}

.lab-website,
.lab-email {
  margin: 10px 0;
}

.lab-position-title {
  flex: 2;
}

.lab-position-title p {
  margin-top: 25px;
  margin-bottom: 10px;
}

.lab-position-title h2 {
  font-size: 32px;
  margin: 0;
  margin-bottom: 20px;
  text-align: start;
}

.professor-info {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.professor-info > div {
  flex: 1 1 30%;
  margin-bottom: 10px;
}

.lab-info p {
  margin: 5px 0;
}

.lab-details-section,
.lab-expectations {
  margin-bottom: 20px;
}

.lab-details-section h4,
.lab-expectations h4 {
  margin-bottom: 10px;
}

.apply-button {
  display: block;
  width: 100%;
  padding: 15px;
  background-color: #006C18;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;
  text-align: center;
}

.apply-button:hover {
  background-color: #004d00;
}

.confirmation {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.confirmation p {
  max-width: 450px;
  padding-top: 30px;
  padding-bottom: 30px;
  font-size: 18px;
  text-align: center;
}

.confirmation div {
  display: flex;
  flex-direction: row;
}
.confirmation button {
  display: block;
  min-width: 130px;
  padding: 15px;
  background-color: #006C18;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;
  text-align: center;
  margin-left: 20px;
  margin-right: 20px;
}