
.ReportEntry {
  display: flex;
  padding-right: 10px;
  padding-left: 5px;
  padding-top: 5px;
  padding-bottom: 5px;
  border: 2px solid black;
  margin-bottom: 5px;
}

.ReportEntry:hover {
  background-color: rgb(240, 240, 240);
}

.ReportDate,
.ReportTitle,
.ReportAuthor{
  font-size: 12px;
}

.ReportDate {
  flex: 2;
  padding-right: 3px;
}

.ReportTitle {
  flex: 4;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.ReportAuthor {
  flex : 2;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
