
.PortfolioOverlay.hidden {
  display: none;
}

.PortfolioOverlay.visible {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.4);
  z-index: 100;
}

.PopUp {
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  width: 70%;
  height: 80%;
  transform: translate(-50%, -50%);
  background-color: white;
  border-radius: 20px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 20px;
  overflow: hidden;
}

.PopUpExitButton {
  position: fixed;
  top: 5%;
  left: 4%;
  background-color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 50%;
  font-size: 25px;
  cursor: pointer;
}

.PortfolioWrapper {
  display: block;
  width: 100%;
  height: 100%;
}

.PortfolioDetails {
  width: 100%;
  height: 100%;
  display: flex;
  padding-left: 80px;
  padding-top: 40px;
}

.PortfolioTable {
  padding: 1px 1px;
  width: 90%;
  display: grid;
  grid-template-columns: 200px 1fr;
  gap: 1px;
  overflow-y: auto;
}

.PortfolioTable label, 
.PortfolioTable p,
.PortfolioTable span{
  padding: 10px;
  background-color: #F2F4F8;
  color: black;
}

.PortfolioTable label {
  margin: 0;
}

