.profile-wrapper {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--light-gray);
  padding-top: 5vh;
  padding-bottom: 5vh;
  padding-left: 18vh;
  padding-right: 18vh;
}

.ProfileInside-wrapper {
  background-color: white;
  height: 100%;
  width: 100%;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
} 

.ProfileInsideHeader-wrapper {
  /* width: 100%;
  height: 20%; */
  flex: 1;
  /* padding-left: 12vh; */
  padding-top: 10vh;
  display: flex;
  flex-shrink: 0;
}

.Profile-header {
  color: var(--black);
  /* font-size: 55px; */
  font-size: 44px;
  font-weight: bold;
}

.ProfileInsideMain-wrapper {
  width: 100%; 
  height: 80%;
  display: flex;
  flex-direction: row;
}

.ProfileInsideMainLeft-wrapper {
  flex: 1;
  padding-left: 12vh;
  padding-right: 6vh;
  padding-top: 6vh;
}

.ProfileUserId-wrapper,
.ProfileConfirmPassword-wrapper,
.ProfilePassword-wrapper,
.ProfileEmail-wrapper,
.ProfileName-wrapper {
  display: flex;  
  flex-direction: column;
}

.ProfilePasswordInputBar-wrapper{
  position: relative;
}

.ProfileConfirmPasswordInputBar-wrapper{
  position: relative;
}

.ProfileConfirmPassword-text,
.ProfileUserId-text,
.ProfilePassword-text,
.ProfileEmail-text,
.ProfileName-text {
  color: var(--black);
  font-size: 12px;
  font-weight: 600;
  text-align: left;
  margin-bottom: 1vh;
}

.ProfileConfirmPassword-bar,
.ProfileUserId-bar,
.ProfilePassword-bar,
.ProfileEmail-bar,
.ProfileName-bar {
  height: 5vh;
  border: none;
  background-color: rgb(240, 240, 240);
  padding-left: 10px;
  margin-bottom: 3vh;
}

.ProfileConfirmPassword-bar,
.ProfilePassword-bar{
  padding-right: 30px;
  width: 100%;
}

.ProfilePassword-icon{
  position: absolute;
  right: 10px;
  cursor: pointer;
  top: 50%;
  transform: translateY(-88%);
  color: black;
}

.ProfileConfirmPassword-text.disabled,
.ProfileUserId-text.disabled{
  color: rgb(188, 188, 188);
} 

.ProfileConfirmPassword-bar.disabled,
.ProfileUserId-bar.disabled {
  background-color: rgb(188, 188, 188);
  padding-left: 10px;
  margin-bottom: 3vh;
  height: 5vh;
  border: none;
}

.ProfileButton-wrapper {
  display: flex;
  justify-content: center;
}

.Profile-button {
  background-color: rgb(0, 108, 24);
  color: white;
  font-weight: bold;
  text-align: center;
  border: none;
  height: 6vh;
  width: 16vh;
  border-radius: 8px;
  transition: opacity 0.15s;
  
}

.Profile-button.disabled {
  background-color: gray;
  cursor: not-allowed;
  opacity: 0.6;
}

.Profile-button:hover {
  cursor: pointer;
}

.Profile-button:active {
  opacity: 0.8;
}

.ProfileInsideMainRight-wrapper {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding-right: 20px;
}

.ProfileAccount-wrapper {
  /* width : 100%;
  height: 50%; */
  flex: 3;
  width : 100%;
  /* margin-top: -100px; */
  display: flex;
  align-items: center;
  justify-content: center;
}

.ProfileAccount-logo {
  width: 100%;
  max-width: 200px; /* Set a maximum width */
  max-height: 200px; /* Set a maximum height */
  object-fit: cover; /* Contain the image within the specified dimensions */
  border-radius: 50%; /* Optional: To make the image circular */
  margin: 0;
}

.ProfileChangePicture-wrapper{
  /* width: 100%;
  height:10%; */
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.ProfileChangePicture-text{
  color: black;
  width: 220px;  
  font-size: 16px;
}

.ProfileChangePicture-button{
  width: 10%;
  margin: 0;
  margin-right: 10px;
  background-color: white;
  border: none;
  transition: 0.15s;
}

.ppinput {
  position: relative;
  left: 30px;
}

.ProfileChangePicture-button:hover{
  cursor: pointer;
}

.ProfileChangePicture-button:active{
  opacity: 0.7;
}

.ProfileChangePicture-icon{
  width: 100%;
}
