
.MailEntry-false{
  display: flex;
  padding-right: 10px;
  padding-left: 5px;
  padding-top: 5px;
  padding-bottom: 5px;
  border: 2px solid black;
  margin-bottom: 5px;
  
}

.MailEntry-true{
  display: flex;
  padding-right: 10px;
  padding-left: 5px;
  padding-top: 5px;
  padding-bottom: 5px;
  border: 2px solid black;
  margin-bottom: 5px;
  opacity: 0.5;
}

.MailEntry:hover {
  background-color: rgb(240, 240, 240);
}

.MailDate,
.MailTitle,
.MailSendRec{
  font-size: 12px;
}

.MailDate {
  flex: 4;
}

.MailTitle {
  flex: 10;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.MailRecipient {
  flex: 7;
  text-align: right;
}