
.MailOverlay.hidden {
  display: none;
}

.MailOverlay.visible {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.4);
  z-index: 100;
}

.PopUp {
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  width: 50%;
  height: 60%;
  transform: translate(-50%, -50%);
  background-color: white;
  border-radius: 20px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 20px;
}

.PopUpExitButton {
  position: fixed;
  top: 5%;
  left: 4%;
  background-color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 50%;
  font-size: 25px;
  cursor: pointer;
}

.MailWrapper {
  display: block;
  width: 100%;
  height: 100%;
}

.MailDetails {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding-left: 80px;
}

.MailDetailsHeader{
  flex: 3;
  display: flex;
  flex-direction: column;
  width: 80%;
  justify-content: flex-end;
  margin-bottom: 20px;
  padding-top: 20px;
}

.MailDetailsSendRec, 
.MailDetailsDate,
.MailDetailsTitle{
  font-weight: bold;
  font-family: Roboto, Arial;
  font-size: 15px;
}

.MailDetailsContent {
  flex: 7;
  width: 90%;
  padding-left: 10px;
  padding-bottom: 20px;
  overflow: auto;
  color: black;
  white-space: pre-wrap;
}