
.ReportOverlay.hidden {
  display: none;
}

.ReportOverlay.visible {
  display: 'block';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.4);
  z-index: 100;
}

.PopUp {
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  width: 50%;
  height: 60%;
  transform: translate(-50%, -50%);
  background-color: white;
  border-radius: 20px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 20px;
}

.PopUpExitButton {
  position: fixed;
  top: 5%;
  left: 4%;
  background-color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 50%;
  font-size: 25px;
  cursor: pointer;
}

.ReportWrapper {
  display: block;
  width: 100%;
  height: 100%;
}

.ReportDetails {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding-left: 80px;
}

.ReportDetailsHeader{
  flex: 3;
  display: flex;
  flex-direction: column;
  width: 80%;
  justify-content: flex-end;
  margin-bottom: 20px;
  padding-top: 20px;
}

.ReportDetailsSender, 
.ReportDetailsDate,
.ReportDetailsTitle{
  font-weight: bold;
  font-family: Roboto, Arial;
  font-size: 15px;
}

.ReportDetailsContent {
  flex: 7;
  width: 90%;
  padding-left: 10px;
  padding-bottom: 20px;
  overflow: auto;
  color: black;
  white-space: pre-wrap;
}

.ReportDetailsFooter {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-bottom: 5px;
}

.ReportFileDownloadButton {
  background-color: #006C18;
  padding: 10px 25px;
  border: none;
  border-radius: 10px;
  margin-right: 10px;

  color: white;
  font-family: Roboto, Arial;
  font-weight: bold;

  cursor: pointer;
  transition: opacity 0.15s;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.ReportFileDownloadButton:hover {
  opacity: 0.7;
}

.ReportFileDownloadButton:active {
  opacity: 0.4;
}

.ReportFileDownloadLabel {
  margin-top: 5px;
  padding-right: 50px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
