.ProfessorLabInfo-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  background-color: #f4f4f4;
}

.ProfessorLabInfo-container {
  background-color: white;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 600px;
  margin: 0 auto;
  margin-top: 3rem;
  margin-bottom: 2rem;
  overflow-y: auto;
}

.ProfessorLabInfo-h1 {
  text-align: center;
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 20px;
}

.ProfessorLabInfo-label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.ProfessorLabInfo-container input,
.ProfessorLabInfo-container select,
.ProfessorLabInfo-container textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
}

.ProfessorLabInfo-container textarea {
  resize: vertical;
}

.checkbox-group {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.checkbox-group label {
  flex: 2;
}
.checkbox-group input {
  flex: 1;
}

.ProfessorLabInfo-button {
  display: block;
  width: 100%;
  padding: 10px;
  background-color: #006C18;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;
}

.ProfessorLabInfo-button:hover {
  background-color: #004d00;
}
